@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
#grid{
    display: grid;
    grid-template-rows: minmax(5rem,auto) minmax(calc(100vh - 5rem - 10rem),auto) minmax(10rem,auto);
    grid-template-areas: "navbar" "content" "footer";
}
#navbar{
    grid-area: navbar;
}
#content{
    grid-area: content;
    background-color: rgb(255, 176, 176);
}
#footer{
    grid-area: footer;
    background-color: rgb(137, 193, 226);
}
*{
    margin: 0;
    padding: 0;
}
.box-element {
    box-shadow: hsl(0, 0%, 60%) 0 0 16px;
    background-color: #fff;
    border-radius: 4px;
    padding: 10px;
}

.nav-container{
    background-color:#eceef1 ;
    border: 2px solid coral;
    height: 5rem;
    font-family: Pacifico,Arial, Helvetica, sans-serif;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.projname{
    font-size: calc(1.5vw + 1.5em);
    color: coral;
}


.content-container{
    display: grid;
    grid-template-columns: 1fr;
    padding:1rem 0.5rem;

}
.lecture{
    background-color: rgb(117, 247, 186);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 1rem 0.5rem;
    margin-bottom: 2rem;
    
}
.join-btn{
    padding: 0.5rem 1.5rem;
    outline: blue;
    background-color: rgb(172, 136, 240);
    border-radius: 0.5rem;
    color: white;
}

.join-btn:hover{
    background-color: rgb(37, 21, 37);
}

@media screen and (min-width:720px){
    .content-container{
        padding:1rem 5rem;
    }
    .join-btn{
        padding: 0.5rem 3rem;
    }
    .lecture{
        padding: 1rem 3rem;
    }
}

@media screen and (min-width:960px){
    .content-container{
        padding:1rem 9rem;
    }
    .join-btn{
        border-radius: 1rem;
        font-size: 2.2rem;
    }
}

@media screen and (min-width:1080px){
    .content-container{
        padding:1rem 12rem;
    }
}
.footer-container{
    display:grid;
    -webkit-justify-content: center;
            justify-content: center;
    grid-template-columns: 1fr;
    padding: 1rem 0.5rem;
}
.social-item{
    font-size:2rem;
    
}

